                </view>
            </scroll-view>
            <!-- 运输中-列表 -->
            <scroll-view v-if="tabCur == 4" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[3].list" :key="index">
                    <!-- 货物信息 -->
                    <view class="car_info" :data-id="item.orderId" @tap="linkToOrderCarInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
